import Link from "next/link";
import { GraduationCap, Users } from "lucide-react";

export default function Home() {
    return (
        <main className="flex min-h-screen flex-col items-center justify-center p-4 md:p-24 bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-950 dark:to-slate-900">
            <div className="z-10 max-w-5xl w-full items-center justify-center font-mono text-sm flex flex-col gap-12">
                <h1 className="text-4xl md:text-6xl font-bold text-center tracking-tighter bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-indigo-600">
                    University ERP
                </h1>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-8 w-full max-w-3xl">
                    <Link href="/student/dashboard" className="group relative overflow-hidden rounded-2xl border bg-white/50 p-8 hover:bg-white hover:shadow-xl transition-all duration-300 dark:bg-slate-900/50 dark:hover:bg-slate-900 backdrop-blur-sm">
                        <div className="flex flex-col items-center text-center gap-4">
                            <div className="p-4 rounded-full bg-blue-100 text-blue-600 group-hover:scale-110 transition-transform duration-300">
                                <GraduationCap size={48} />
                            </div>
                            <h2 className="text-2xl font-bold">Student Portal</h2>
                            <p className="text-muted-foreground">View grades, attendance, and course details.</p>
                        </div>
                    </Link>

                    <Link href="/faculty/dashboard" className="group relative overflow-hidden rounded-2xl border bg-white/50 p-8 hover:bg-white hover:shadow-xl transition-all duration-300 dark:bg-slate-900/50 dark:hover:bg-slate-900 backdrop-blur-sm">
                        <div className="flex flex-col items-center text-center gap-4">
                            <div className="p-4 rounded-full bg-emerald-100 text-emerald-600 group-hover:scale-110 transition-transform duration-300">
                                <Users size={48} />
                            </div>
                            <h2 className="text-2xl font-bold">Faculty Portal</h2>
                            <p className="text-muted-foreground">Manage courses, attendance, and grading.</p>
                        </div>
                    </Link>
                </div>
            </div>
        </main>
    );
}
